/* eslint-disable no-var, prefer-const, prefer-arrow/prefer-arrow-functions */

import type {
    ShakrEditor,
    ShakrEditorAppearanceType,
    ShakrEditorError,
    ShakrEditorErrorEvent,
    ShakrEditorGetMediaEvent,
    ShakrEditorMedia,
    ShakrEditorMediaLibrary,
    ShakrEditorOpenMediaLibraryEvent,
    ShakrEditorOptions,
    ShakrEditorTheme,
    ShakrEditorThemeCustomizeOptions,
    ShakrreadyEditor,
    ShakrreadyEditorError,
    ShakrreadyEditorErrorEvent,
    ShakrreadyEditorOptions
} from '@shakrmedia/editor-sdk-types';

var SHAKR_SDK_SCRIPT_ID = 'shakr-sdk-script';
var SDK_SCRIPT_URL = 'https://sdk.shakr.com/sdk.js';

export type {
    ShakrEditor,
    ShakrEditorAppearanceType,
    ShakrEditorError,
    ShakrEditorErrorEvent,
    ShakrEditorGetMediaEvent,
    ShakrEditorMedia,
    ShakrEditorMediaLibrary,
    ShakrEditorOpenMediaLibraryEvent,
    ShakrEditorOptions,
    ShakrEditorTheme,
    ShakrEditorThemeCustomizeOptions,
    ShakrreadyEditor,
    ShakrreadyEditorError,
    ShakrreadyEditorErrorEvent,
    ShakrreadyEditorOptions
};

function loadShakrSDK(): Promise<void> {
    return new Promise<void>(function (resolve, reject): void {
        var onLoad = function (): void {
            resolve();
        };

        if ((window as any).ShakrEditor) {
            return onLoad();
        }

        var existing = document.querySelector<HTMLScriptElement>(`#${SHAKR_SDK_SCRIPT_ID}`);
        var script = existing || document.createElement('script');

        script.setAttribute('id', SHAKR_SDK_SCRIPT_ID);

        script.src = SDK_SCRIPT_URL;
        script.onload = onLoad;
        script.onerror = function (e): void {
            reject(e);
        };

        if (!existing) {
            document.head.appendChild(script);
        }
    });
}

export function createShakrEditor(options: ShakrEditorOptions): Promise<ShakrEditor> {
    return loadShakrSDK().then(function (): Promise<ShakrEditor> {
        return (window as any).createShakrEditor(options);
    });
}

export function createShakrreadyEditor(
    options: ShakrreadyEditorOptions
): Promise<ShakrreadyEditor> {
    return loadShakrSDK().then(function (): Promise<ShakrreadyEditor> {
        return (window as any).createShakrreadyEditor(options);
    });
}
